<template>
  <div id="app" class="container pt-5">
    <div class="text-center">
      <h1 class="display-3"></h1>
      <p></p>
      <div class="row form-group">
        <div class="input-group col-6 offset-3">
          <input type="text" v-model="dummy_phone_number" class="form-control form-control-lg">
          <div class="input-group-append">
            <button class="btn btn-primary" type="button" v-on:click="generate"></button>
          </div>
        </div>
      </div>
    </div>
    <hr />
    <div class="qa">
      <p class="lead"></p>
      <p><a href="http://www.soumu.go.jp/main_sosiki/joho_tsusin/top/tel_number/number_shitei.html" target="_blank">
  </a></p>
      <p class="lead"></p>
      <p>30519</p>
    </div>
  </div>
</template>
<script>
const unusedPhoneIndexs = require('./data.json')

export default {
  name: 'app',
  data () {
    return {
      dummy_phone_number: ''
    }
  },
  methods: {
    generate: function () {
      const index = this.arrayShuffle(unusedPhoneIndexs)[0]
      this.dummy_phone_number = `${index}${this.randomNum()}`
    },
    arrayShuffle: function(a) {
      for (let i = a.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [a[i], a[j]] = [a[j], a[i]];
      }
      return a;
    },
    randomNum: function(){
      const min = 10000
      const max = 99999
      return Math.floor( Math.random() * (max + 1 - min) ) + min
    }
  },
  created: function () {
    this.generate()
  }
}
</script>

<style lang="scss">
body {
  background-color: #f5f5f5;
}
.qa {
  font-size: 12px;
}
</style>



